      &lt;/div&gt;<br>
    &lt;/dy-uploader&gt;<br>
</codeField>
<codeField>
&lt;script&gt;<br>
import dyIcon from '@/components/dy-icon'<br>
export default {<br>
  components:{dyIcon}<br>
  methods: {<br>
    uploadSuccess () {<br>
    },<br>
    uploadError (msg) {<br>
      this.$message({<br>
        message: msg,<br>
        autoClose: false,<br>
        type: 'error'<br>
      })<br>
    }<br>
  }<br>
}<br>
&lt;/script&gt;<br>
</codeField>
